<template>
  <div>
    <Alert />
    <div>
      <div class="text-right mt-4">
        <button class="btn btn-primary" @click="openProductModal(true)">建立新產品</button>
      </div>
      <table class="table mt-4">
        <thead>
          <tr>
            <th width="100">分類</th>
            <th>產品名稱</th>
            <th width="100">原價</th>
            <th width="100">售價</th>
            <th width="100">是否啟用</th>
            <th class="d-none d-md-table-cell" width="120">編輯</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="item in products" :key="item.id">
            <td>{{ item.category }}</td>
            <td>{{ item.title }}</td>
            <td class="text-right">{{ item.origin_price | currency }}</td>
            <td class="text-right">{{ item.price | currency }}</td>
            <td>
              <span v-if="item.is_enabled" class="text-success">啟用</span>
              <span v-else>未啟用</span>
            </td>
            <td class="d-none d-md-table-cell">
              <button
                class="btn btn-outline-primary btn-sm mr-1"
                @click="openProductModal(false, item)">編輯</button>
              <button
                class="btn btn-outline-danger btn-sm"
                @click="openProductDelModal(item)">刪除</button>
            </td>
          </tr>
        </tbody>
      </table>
      <Pagination
        class="d-flex justify-content-center"
        :pages="pagination"
        @getThisPage="getProducts"
        v-if="pagination.total_pages > 1"
      />
      <!-- 新增產品 Modal -->
      <div
        class="modal fade"
        id="productsModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-lg" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-dark text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span>新增產品</span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              <div class="row">
                <div class="col-sm-4">
                  <div class="form-group">
                    <label for="image">輸入圖片網址</label>
                    <input
                      type="text"
                      class="form-control"
                      id="image"
                      placeholder="請輸入圖片連結"
                      v-model="tempProduct.imageUrl"
                    >
                  </div>
                  <div class="form-group">
                    <label for="customFile">
                      或 上傳圖片
                      <i class="fas fa-spinner fa-spin" v-if="fileupLoading"></i>
                    </label>
                    <input
                      type="file"
                      id="customFile"
                      class="form-control"
                      ref="files"
                      @change="upFile"
                    >
                  </div>
                  <img
                    class="img-fluid"
                    :src="tempProduct.imageUrl || productImgurl"
                  >
                </div>
                <div class="col-sm-8">
                  <div class="form-group">
                    <label for="title">標題</label>
                    <input
                      type="text"
                      class="form-control"
                      id="title"
                      placeholder="請輸入標題"
                      v-model="tempProduct.title"
                    >
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="category">分類</label>
                      <input
                        type="text"
                        class="form-control"
                        id="category"
                        placeholder="請輸入分類"
                        v-model="tempProduct.category"
                      >
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price">單位</label>
                      <input
                        type="unit"
                        class="form-control"
                        id="unit"
                        placeholder="請輸入單位"
                        v-model="tempProduct.unit"
                      >
                    </div>
                  </div>

                  <div class="form-row">
                    <div class="form-group col-md-6">
                      <label for="origin_price">原價</label>
                      <input
                        type="number"
                        class="form-control"
                        id="origin_price"
                        placeholder="請輸入原價"
                        v-model="tempProduct.origin_price"
                      >
                    </div>
                    <div class="form-group col-md-6">
                      <label for="price">售價</label>
                      <input
                        type="number"
                        class="form-control"
                        id="price"
                        placeholder="請輸入售價"
                        v-model="tempProduct.price"
                      >
                    </div>
                  </div>
                  <hr>

                  <div class="form-group">
                    <label for="description">產品描述</label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="description"
                      placeholder="請輸入產品描述"
                      v-model="tempProduct.description"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <label for="content">說明內容</label>
                    <textarea
                      type="text"
                      class="form-control"
                      id="content"
                      placeholder="請輸入產品說明內容"
                      v-model="tempProduct.content"
                    ></textarea>
                  </div>
                  <div class="form-group">
                    <div class="form-check">
                      <input
                        class="form-check-input"
                        type="checkbox"
                        id="is_enabled"
                        v-model="tempProduct.is_enabled"
                        :true-value="1"
                        :false-value="0"
                      >
                      <label class="form-check-label" for="is_enabled">是否啟用</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button type="button"
                class="btn btn-outline-secondary"
                data-dismiss="modal">取消</button>
              <button type="button"
                class="btn btn-primary"
                @click="updateProduct">確認</button>
            </div>
          </div>
        </div>
      </div>
      <!-- 刪除產品 Modal -->
      <div
        class="modal fade"
        id="delProductModal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog" role="document">
          <div class="modal-content border-0">
            <div class="modal-header bg-danger text-white">
              <h5 class="modal-title" id="exampleModalLabel">
                <span>刪除產品</span>
              </h5>
              <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
              </button>
            </div>
            <div class="modal-body">
              是否刪除
              <strong class="text-danger">{{ this.tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
            </div>
            <div class="modal-footer">
              <button type="button"
                class="btn btn-outline-secondary"
                data-dismiss="modal">取消</button>
              <button type="button"
                class="btn btn-danger"
                @click="delProduct">確認刪除</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.viewblock{
  overflow-x: auto;
}
</style>

<script>
import { mapGetters, mapActions } from 'vuex';
import Pagination from '../components/Pagination.vue';
import Alert from '../components/AlertMessage.vue';

export default {
  data() {
    return {
      tempProduct: {},
    };
  },
  methods: {
    getProducts(page = 1) {
      this.$store.dispatch('backstageModule/getProducts', page);
    },
    openProductModal(isNew, item) {
      const vm = this;
      vm.tempProduct = Object.assign({}, item);
      this.$store.dispatch('backstageModule/openProductModal', { isNew, item });
    },
    updateProduct() {
      const vm = this;
      this.$store.dispatch('backstageModule/updateProduct', vm.tempProduct);
    },
    openProductDelModal(item) {
      const vm = this;
      vm.tempProduct = Object.assign({}, item);
      this.$store.dispatch('backstageModule/openProductDelModal', item);
    },
    upFile() {
      const upedFile = this.$refs.files.files[0];
      this.$store.dispatch('backstageModule/upFile', upedFile);
    },
    ...mapActions('backstageModule', ['delProduct']),
  },
  created() {
    this.getProducts();
  },
  computed: {
    ...mapGetters('backstageModule', ['pagination', 'products', 'fileupLoading', 'productImgurl']),
  },
  components: {
    Pagination,
    Alert,
  },
};
</script>
